<template>
<div class="shopInfo">
	<ul class="header">
		<li>
			<em>起送价</em>
			<p>
				<span>20</span>
				<i>元</i>
			</p>
		</li>
		<li>
			<em>商家配送</em>
			<p>
				<span>4</span>
				<i>元</i>
			</p>
		</li>
		<li>
			<em>平均配送时间</em>
			<p>
				<span>38</span>
				<i>分钟</i>
			</p>
		</li>
	</ul>
	<dl class="notice part">
		<dt>活动公告</dt>
		<dd>
			<p>粥品香坊其烹饪粥料的秘方源于中国千年古法，在融和现代制作工艺，由世界烹饪大师屈浩先生领衔研发。坚守纯天然、0添加的良心品质深得消费者青睐，发展至今成为粥类的引领品牌。是2008年奥运会和2013年园博会指定餐饮服务商。</p>
			<ul>
				<li><i class="jian">减</i>在线支付满28减5</li>
				<li><i class="zhe">折</i>VC无限橙果汁全场8折</li>
				<li><i class="te">特</i>单人精彩套餐</li>
				<li><i class="piao">票</i>该商家支持发票,请下单写好发票抬头</li>
				<li><i class="bao">保</i>已加入“外卖保”计划,食品安全保障</li>
			</ul>
		</dd>
	</dl>
	<dl class="photos part">
		<dt>商家实景</dt>
		<dd class="w oh"> 
			<img v-for="(p,index) in buildArray(photos)" :key="index" :src="p" alt="" v-preview="p" preview-title-enable="true" preview-nav-enable="true" />
		</dd>
	</dl>
	<dl class="info part">
		<dt>商家信息</dt>
		<dd>
			<p>该商家支持发票,请下单写好发票抬头</p>
			<p>品类：其他菜系,包子粥店</p>
			<p>北京市昌平区回龙观西大街龙观置业大厦底商B座102单元1340</p>
			<p>营业时间：10:00-20:30</p>
		</dd>
	</dl>
</div>
</template>

<script>
import imagePreview from 'vue-image-preview';
export default {
	name: 'shopInfo',
	props:["shop"],
	data () {
		return {
			shopId: this.$route.params.shopId,
			photos:['871c5cf5715740998d5040dda6e66abfjpeg',
					'b6c75bd250e5ba69868f3b1178afbda3jpeg',
					'f963d608c5811bc2d902fc9ab9a5baa7jpeg']
		}
	},
	watch: {
	
	},
	created(){
	
	},
	computed: {
	
	},
	methods: {
		buildArray(list){
			let arr=[];
			list.forEach(item=>{
				arr.push(this.imageSrcBuild(item));
			})
			return arr;
		},
		imageSrcBuild(str){
			let a=str.substr(0,1);
			let b=str.substr(1,2);
			let c=str.substr(3);
			return `http://fuss10.elemecdn.com/${a}/${b}/${c}.jpeg`;
		}
	}
}
</script>

<style scoped lang="less">
.shopInfo{
	.header{display: flex;justify-content: center;padding:30px 0;border-top: 1px solid #eee;
		li{width: 33.33%;text-align: center;
			&:nth-child(2){border-left: 1px solid #eee;border-right: 1px solid #eee;}
			em{color:#999;}
			p{padding:10px 0;
				span{font-size: 24px;color: #333;}
				i{font-style: normal;color:#999;}
			}
		}
	}
	.part{padding:15px 5%;box-sizing: border-box;border-top: 10px solid #eee;
		dt{font-size: 14px;color:#333;line-height: 40px;}
		dd{font-size: 12px;color: #666;line-height: 25px;}
	}
	.photos dd img{width:23%;float:left;border-radius: 2px;height: auto;display: block;margin-right:2.666%;}
	.photos dd img:nth-child(4n){margin-right: 0;}
	.notice p{color:#f90;margin-bottom: 10px;}
	.notice dd i{font-style: normal;padding:2px;font-family:SimSun;font-size: 12px;margin-right: 5px;border-radius: 2px;}
	.jian,.zhe,.te{color:#fff;}
	.jian{background-color: #F02121;}
	.zhe{background-color: #008BC7;}
	.te{background-color: #07B641;}
	.piao{color: #9D2290;border:1px solid #C478BC;box-sizing:border-box;}
	.bao{color:#0BCE9D;border:1px solid #0BCE9D;box-sizing:border-box;}
	.notice dd ul li{height: 30px;line-height: 30px;}
}
</style>


