<template>
  <div id="app">
    <router-view/>
    <MainFooter v-if="showMainFooter"></MainFooter>
    <lg-preview></lg-preview>
  </div>
</template>

<script>
import MainFooter from './components/MainFooter'
export default {
  name: 'App',
  components:{MainFooter},
  data () {
    return {
      showMainFooter:true
    }
  },
  created(){
  	if(!navigator.onLine){
  		this.$toast("饿了么：连接网络失败，请检查网络后重试");
  		return false;
  	}
    let pathName=this.$route.name;
    if(pathName==='Index' || pathName==='findIndex' || pathName==='orderList' || pathName==='profile'){
      this.showMainFooter=true;
    }else{
      this.showMainFooter=false;
    }
  }
}
</script>

<style>

@import url("http://at.alicdn.com/t/font_959376_h80o18uw9st.css");
@import url("./assets/style/base.css");
#app {width:auto;min-width:100%;height:auto;min-height:100%;font-family: 'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;}
.mint-indicator{z-index: 100;position: relative;}
.index .hotMenu .swiper-pagination span{height: 2px;border-radius: 5px;width:10px;}
#fly{position:fixed;z-index:200;width:16px;height:16px;background-color:#2396ff;border-radius:100%;font-size:10px;color:#fff;text-align: center;line-height: 16px;
transition: 1s margin-top cubic-bezier(.15, -0.75, .88, .33), 1s margin-right cubic-bezier(0, 0, 0, 0);
} 
</style>


